<template>
  <div id="myZone">
    <head-top title="个人中心" :show-back="false"></head-top>
    <div class="myzoneFaceWwall">
      <div class="myFace">
        <div class="myFaceImg">
          <van-uploader
            capture="camera"
            preview-image
            :before-read="beforeRead"
            :after-read="afterRead"
          >
            <img :src="userPhoto" />
          </van-uploader>
        </div>
        <div class="MyFaceName">{{userName}}</div>
        <div class="maFaceFt"></div>
      </div>
    </div>
    <div class="myZoneContent">
      <!-- <router-link> -->
      <div
        class="myZoneContentItem"
        v-for="item in Details"
        :key="item.info"
        @click="goTO(item.path)"
      >
        <span class="info">{{item.info}}</span>
        <i class="iconfont fL" :class="item.icon"></i>
        <i class="iconfont iconqianjin fR"></i>
      </div>
      <!-- </a> -->
      <!-- </router-link> -->
    </div>
    <div class="myZoneFooter">
      <van-button
        type="primary"
        :loading="loading"
        loading-text="退出登录中..."
        @click="courseEnter"
      >退出登录</van-button>
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import imgDemo from "./../assets/boy_img.png";
import { Button, Uploader } from "vant";

export default {
  name: "",
  components: { [Button.name]: Button, [Uploader.name]: Uploader, headTop },
  props: {},
  data() {
    return {
      userName:
        (this.$store.getters.student &&
          this.$store.getters.student["StudentName"]) ||
        "",
      userPhoto: imgDemo,
      loading: false,
      Details: [
        {
          path: "/personalDatas",
          info: "个人资料",
          icon: "icongerenziliao"
        },
        // {
        //   path: "/myStats",
        //   info: "我的统计",
        //   icon: "icontongji"
        // },
        {
          path: "/aboutUs",
          info: "关于我们",
          icon: "iconguanyuwomen"
        }
      ]
    };
  },
  computed: {},
  watch: {},
  created() {},
  methods: {
    // 返回布尔值
    beforeRead(file) {
      if (
        file.type !== "image/jpeg" &&
        file.type !== "image/png" &&
        file.type !== "image/jpg"
      ) {
        this.$toast("请上传 jpg 格式图片");
        return false;
      }
      // 超过20M
      if (file.size > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      return true;
    },
    afterRead(file) {
      // console.log("file:", file);
      if (file) {
        this.$gatewayHttp.resourse
          .upload({ funType: 1, imgBase64: file["content"] })
          .then(res => {
            if (res.Result) {
              this.userPhoto = file["content"];
            }
          })
          .catch(err => {});
      }
    },
    courseEnter() {
      // console.log("退出登录");
      this.loading = true;
      this.$store
        .dispatch("logout")
        .then(res => {
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
      this.$router.push("/login");
    },
    goBack() {
      this.$router.go(-1);
    },
    goTO(path) {
      console.log(path);
      this.$router.push(path);
    }
  }
};
</script>

<style lang='less'>
#myZone {
  position: relative;
  width: 100%;
  height: 80vh;
  .myzoneFaceWwall {
    width: 100%;
    height: 19vh;
    margin-bottom: 12vh;
    background-image: url("../assets/pesonbg.png");
    background-size: 100vw 19vh;
  }
  .myFace {
    width: 73vw;
    height: 18vh;
    background: rgba(255, 255, 255, 1);
    background-size: cover;
    position: absolute;
    left: 0;
    right: 0;
    top: 9vh;
    margin-left: auto;
    margin-right: auto;
    .myFaceImg {
      width: 13vw;
      height: 7.5vh;
      // background: rgba(0, 203, 171, 1);
      // border-radius: 50%;
      margin: 0 auto;
      margin-top: 2vh;
      background-size: 13vw 7.5vh;
      img {
        max-width: 13vw;
        max-height: 7.5vh;
        border-radius: 50%;
        border: 1px solid #00cbab;
      }
    }
    .MyFaceName {
      height: 1.4rem;
      font-family: MicrosoftYaHei;
      font-weight: 400;
      color: rgba(51, 51, 51, 1);
      line-height: 1.5rem;
      margin: 0 auto;
      margin-top: 4px;
    }

    .maFaceFt {
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      height: 5vh;
      background-image: url("../assets/facebg.png");
      background-size: 100% 5vh;
      background-repeat: no-repeat;
    }
  }

  .myZoneContent {
    width: 100vw;
    padding-bottom: 4vh;
    background: rgba(255, 255, 255, 1);
    .myZoneContentItem {
      width: 94vw;
      height: 8vh;
      border-bottom: 1px solid rgba(221, 221, 221, 1);
      line-height: 8vh;
      text-align: left;
      margin: 0 auto;
      .info {
        display: inline-block;
        font-size: 1.4rem;
        font-family: MicrosoftYaHei;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        margin-left: 4vw;
      }
      .icongerenziliao {
        font-size: 1.6rem;
        color: #21b1f4;
      }
      .iconqianjin {
        font-size: 1.6rem;
        color: #999999;
      }
      .icontongji {
        font-size: 1.6rem;
        color: #fe6b6b;
      }
      .iconguanyuwomen {
        font-size: 1.6rem;
        color: #00cbab;
      }
    }
  }

  .myZoneFooter {
    .van-button--primary {
      width: 64vw;
      height: 6vh;
      background: rgba(0, 203, 171, 1);
      border-radius: 5px;
      margin: 10vh 18vw 7vh 18vw;
      border: 1px solid #00cbab;
    }
  }
}
</style>


